import Swiper, { Pagination, Autoplay } from 'swiper';

Swiper.use([Pagination, Autoplay]);

function sliders() {

  let mainSlider = new Swiper('.main-slider', {
    pagination: {
      el: ".main-slider__pagination",
      clickable: true
    },
    centeredSlides: true,
    slidesPerView: 'auto',
    spaceBetween: 50,
    loop: true,
    autoplay: {
      delay: 3000,
    },
  })
}

sliders();
