import { body_lock, unlock } from "../bodyLock";

const burgerClass = "header__burger"; // класс бургерга
const navClass = "header__info"
const delay = 500; // время задержки

let menuBurger = document.querySelector(`.${burgerClass}`);
let menuNav = document.querySelector(`.${navClass}`);


// переключение бургера меню
if (menuBurger != null) {
  menuBurger.addEventListener("click", function (e) {
    if (unlock) {
      body_lock(delay);
      menuBurger.classList.toggle(`${burgerClass}--active`);
      menuNav.classList.toggle(`${navClass}--active`);
    }
  });
};

function menu_close() {
  menuBurger.classList.remove(`${burgerClass}--active`);
  menuNav.classList.remove(`${navClass}--active`);
}
